import { useEffect, useState } from "react";
import { useAuth, uploadFile } from "./firebase";
import { Link } from "react-router-dom";
import Dropdown from "./dropdown";
import firebase from "firebase/compat/app";
import "firebase/storage";
import { updateProfile, deleteFile } from "firebase/auth";
export default function Profile() {
  const currentUser = useAuth();
  const [photo, setPhoto] = useState(null);
  const [loading, setLoading] = useState(false);
  const [photoURL, setPhotoURL] = useState(
    "https://upload.wikimedia.org/wikipedia/commons/7/7c/Profile_avatar_placeholder_large.png"
  );

  const [isInputVisible, setIsInputVisible] = useState(false);
  function handleRemove() {
    if (currentUser) {
      // Remove photo URL from Firebase Authentication
      updateProfile(currentUser, { photoURL: null })
        .then(() => {
          // Remove photo from Firebase Storage
          const storageRef = firebase
            .storage()
            .ref(`users/${currentUser.uid}/profile.jpg`);
          storageRef
            .delete()
            .then(() => {
              // Reset states
              setPhoto(null);
              setPhotoURL(
                "https://upload.wikimedia.org/wikipedia/commons/7/7c/Profile_avatar_placeholder_large.png"
              );
            })
            .catch((error) => {
              console.error(error);
            });
        })
        .catch((error) => {
          console.error(error);
        });
    }
  }
  function handleChange(e) {
    if (e.target.files[0]) {
      setPhoto(e.target.files[0]);
    }
    var name = document.getElementById("file");
    alert("Selected file: " + name.files.item(0).name);
  }

  function handleClick() {
    uploadFile(photo, currentUser, setLoading);
    setIsInputVisible(false);
  }

  useEffect(() => {
    if (currentUser?.photoURL) {
      setPhotoURL(currentUser.photoURL);
    }
  }, [currentUser]);

  return (
    <div>
      <Link to="/mainpage">
        <p className="backbutton"> BACK</p>
      </Link>

      <div className="profile">
        <img
          src={photoURL}
          className="avatar"
          onClick={() => setIsInputVisible(true)}
          style={{
            width: "200px",
            height: "200px",
            borderRadius: "50%",
            objectFit: "cover",
          }}
        />

        <div className="display" style={{ marginTop: "90px" }}>
          <label htmlFor="file" className="label ">
            choose file
          </label>
          <input
            id="file"
            style={{ visibility: "hidden" }}
            type={"file"}
            onChange={handleChange}
          />
          <button
            className="upload"
            disabled={loading || !photo}
            onClick={handleClick}
          >
            Upload
          </button>
          <button className="remove_pic" onClick={handleRemove}>
            remove
          </button>
        </div>
      </div>
    </div>
  );
}
